import React from "react"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../components/layout"
import Seo from "../components/seo"
import Campaign from "../components/campaign"
import "../styles/main-about-us.css"

export default function AboutUs() {
  return (
    <Layout>
      <Seo title="会社案内" />
      <div className="wrap">

        <section className="mv_abt">
          <div className="mv_abt-inner">
            <p className="txt1">
              <span>会社案内</span>
            </p>
            <p className="txt2">
              人生を豊かにするITの実現を目指し、<br/>
              世界に開かれた視野と、自らの足元を大切に、<br/>
              地道に活動する会社です。
            </p>
          </div>
        </section>

        <section className="president">
          <h2>代表挨拶</h2>
          <div className="president-inner">
            <div className="box1">
              <p>
                　株式会社ホットソウルは、2011年に茅ケ崎の海に近い小さなアパートで創業いたしました。
                社会人一年生からIT業界に従事し、日本のソフトウェア産業が抱える問題点も身をもって体験してきました。
                また自分自身、挫折を経験してこの業界を離れようと考えたこともあります。<br/>
                それでもここまで続けてこれたのは、身近な人の困りごとを解決できた時の喜びや、純粋に好奇心を満たしてくれる技術の面白さがあったからです。
                ITやソフトウェアが持つ可能性を漠然と感じつつ、これまで事業を続けて参りましたが、先日こちらの記事を拝見し、一つの確信を得ました。
              </p>
            </div>
            <div className="box2">
              <div className="flex">
                <div className="flex-item item1">
                  <p>
                    「モスバーガー大崎店 ゆっくりレジ」では、レジに遠隔操作型の分身ロボット「OriHime<br className="pc"/>
                    （オリヒメ）」を設置。<br/>
                    難病や障碍により外出が難しい方が、&quot;パイロット&quot;としてOriHimeを操作して接客を行う。
                    お客様とのお話をしながら、お客様の希望に応じたメニューをご案内する。<br/>
                    <span className="txt1">
                      <a href="https://prtimes.jp/main/html/rd/p/000000032.000019066.html" target="_blank" rel="noopener noreferrer">
                        PRTIMES（2020年7月13日）より
                      </a>
                    </span>
                  </p>
                </div>
                <div className="flex-item item2">
                  <a href="https://prtimes.jp/main/html/rd/p/000000032.000019066.html"　target="_blank" rel="noopener noreferrer">
                    <StaticImage
                      src="../images/main-about-us/ph1.png"
                      alt="遠隔操作型の分身ロボット「OriHime（オリヒメ）」"
                      placeholder="blurred"
                    />
                  </a>
                  <p>遠隔操作型の分身ロボット「OriHime（オリヒメ）」</p>
                </div>
              </div>
            </div>
            <div className="box3">
              <div className="flex">
                <div className="flex-item item1">
                  <p>
                    この事例のように、ITやソフトウェアには、課題は様々あるものの&nbsp;”世界を良い方向に変える大きな力”&nbsp;が秘められていると確信しております。
                    <br/>
                    今後もITやソフトウェアの持つ素晴らしい可能性を追求し、地元神奈川を中心に、身近な人々の豊かな人生の実現に貢献する会社となれるよう、精進して参ります。
                  </p>
                    <span className="flex">
                      <span className="flex-item left">
                        <span className="orange">株式会社ホットソウル</span>
                        <br/>
                        代表取締役
                      </span>
                      <span className="flex-item right">若杉 廣</span>
                    </span>
                </div>
                <div class="flex-item item2">
                  <StaticImage
                      src="../images/main-about-us/ph2.png"
                      alt="代表取締役"
                      placeholder="blurred"
                    />
                </div>
              </div>
            </div>
          </div>
        </section>

        <section className="business clearfix">
          <h2>会社概要</h2>
          <div className="business-bg">
            <div className="business-inner">
              <table>
                <tr className="flex">
                  <th>会社名</th>
                  <td>株式会社ホットソウル</td>
                </tr>
                <tr className="flex">
                  <th>所在地</th>
                  <td>神奈川県藤沢市</td>
                </tr>
                <tr className="flex">
                  <th>設立</th>
                  <td>2011年10月</td>
                </tr>
                <tr className="flex">
                  <th>代表取締役</th>
                  <td>若杉　廣</td>
                </tr>
                <tr className="flex">
                  <th>資本金</th>
                  <td>300万円</td>
                </tr>
                <tr className="flex">
                  <th>事業内容</th>
                  <td className="last">
                    <div className="desital">
                      <h3><span className="bg-yellow">デジタル戦略支援事業</span></h3>
                      システムエンジニアと中小企業診断士の知識・経験を活かした、小規模事業者向けデジタル化支援・IT利活用支援のコンサルティングサービス事業です。
                    </div>
                    <div className="ses">
                      <h3><span className="bg-yellow">SES・受託開発事業</span></h3>
                      Java、PHP等でのWEBシステム開発、運用・保守案件の業務受託や、小規模案件の受託開発を行っております。小規模事業者持続化補助金を活用したホームページ制作やECサイト構築にも対応可能です。
                    </div>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </section>

        <section className="recruit">
          <h2>採用情報</h2>
          <div className="recruit-inner">
            <h3>
              【中途採用】<br/>
              <span className="bg-yellow">ITエンジニア・プログラマー</span>
            </h3>
            <p className="txt1">
              SES契約を基本として売上を確保しつつ、自社製品・サービスの企画・開発を進めるため、数名の正社員を募集しております。<br/>
              中小企業診断士を目指すエンジニア・プログラマー大歓迎！！
            </p>
            <p className="txt1">
              ※現在応募は、ハローワーク経由でのみ受付、とさせて頂いております。
            </p>
          </div>
        </section>
        <section>
          <Campaign/>
        </section>
      </div>
    </Layout>
  )
}