import React from "react"
import { Link } from "gatsby"
import "../styles/campaign.css"

export default function Campaign() {
  return (
    <Link to={`/contact-us`} className="bnr-outer">
      <div className="bnr">
        <p>リニューアルキャンペーン</p>
        <span>今ならご成約者様にもれなくお米5kgプレゼント！</span>
      </div>
    </Link>
  )
}